@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  max-width: 100%;
  position: relative;
}

html,
body {
  height: 100%;
  position: relative;
  font-family: "Poppins", sans-serif;
}

#root {
  height: 100%;
}

.App {
  text-align: center;
  height: 100%;
}

.btn {
  padding: 10px;
  font-size: 16px;
  background-color: #0088a9;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
  font-family: "Poppins", sans-serif;
}

.btn:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: all 0.2s;
}

.hidden {
  display: none;
}

@media screen and (max-width: 600px) {
  .btn {
    font-size: 14px;
    padding: 5px;
  }
}

.home {
  height: 100%;
  background-image: url(/shopping-cart/static/media/background.3ec00b94.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.title {
  padding-top: 10px;
}

.title p {
  font-weight: normal;
}

.shopping {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 4rem;
  padding: 2rem;
  text-align: center;
  background: #202020;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
}

.home p {
  padding: 10px;
  font-size: 18px;
}

@media screen and (max-width: 1200px) {
  .shopping {
    font-size: 3rem;
    padding: 1.5rem;
  }
}

@media screen and (max-width: 910px) {
  .shopping {
    font-size: 2.5rem;
    padding: 1.2rem;
  }

  .title h1 {
    font-size: 1.4rem;
  }

  .title p {
    font-size: 15px;
    font-weight: normal;
  }
}

@media screen and (max-width: 754px) {
  .shopping {
    font-size: 2rem;
    padding: 1rem;
  }

  .title h1 {
    font-size: 1.2rem;
  }

  .title p {
    font-size: 14px;
  }
}

@media screen and (max-width: 480px) {
  .shopping {
    font-size: 1.5rem;
    padding: 14px;
  }

  .title h1 {
    font-size: 1.2rem;
  }

  .title p {
    font-size: 14px;
  }
}

.header h1 {
  padding: 10px;
}

.categories {
  padding: 5px;
  margin: 5px;
  font-size: 16px;
  outline: none;
  border: 1px solid slategray;
  border-radius: 5px;
  font-family: "Poppins", sans-serif;
}

.shop {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
  padding: 1.5rem;
}

.product {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  border: 4px solid slategray;
  border-radius: 10px;
  padding: 1rem;
}

.product > * {
  padding: 10px;
}

.image {
  height: 175px;
  object-fit: contain;
}

.title {
  font-weight: bolder;
  font-size: 18px;
}

.price {
  font-weight: 600;
  font-size: 1.2rem;
}

.sticky {
  padding: 10px;
  font-size: 18px;
  position: fixed;
  top: 100px;
  right: 0;
  z-index: 10;
  border: none;
  background-color: darkorange;
  color: white;
  cursor: pointer;
  font-size: 1.3rem;
  outline: none;
}

.product input {
  max-width: 40px;
  padding: 5px;
}

@media screen and (max-width: 768px) {
  .shop {
    grid-gap: 1rem;
    gap: 1rem;
    padding: 1.5rem;
  }

  .product {
    padding: 10px;
  }

  .image {
    height: 150px;
  }

  .title {
    font-size: 16px;
  }

  .price {
    font-size: 1rem;
  }
}

.cart {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: bolder;
  font-size: 17px;
}

.cart__item {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  border: 4px solid slategray;
  width: 60%;
  margin: 10px;
  grid-gap: 1rem;
  gap: 1rem;
  justify-items: center;
  padding: 1rem;
}

.cart__item .image {
  grid-row: span 2;
}

.info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.info > * {
  padding: 10px;
}

.options {
  padding: 10px;
}

.options > * {
  margin: 10px;
}

.empty {
  position: absolute;
  top: 50vh;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 2rem;
}

.empty p {
  padding: 10px;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #f3f3f3;
  border-radius: 10px;
  padding: 2rem;
  text-align: center;
  width: 40%;
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.3);
  z-index: 10;
  font-size: 18px;
}

.modal > p {
  margin: 5px;
  padding: 10px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  z-index: 5;
}

@media screen and (max-width: 992px) {
  .cart {
    font-size: 16px;
  }

  .cart__item {
    width: 80%;
  }

  .modal {
    padding: 1.5rem;
    width: 50%;
    font-size: 16px;
  }
}

@media screen and (max-width: 600px) {
  .cart {
    font-size: 14px;
  }

  .cart__item {
    width: 90%;
  }

  .empty {
    font-size: 1.2rem;
  }

  .modal {
    padding: 1.3rem;
    width: 70%;
    font-size: 14px;
  }
}

@media screen and (max-width: 400px) {
  .cart {
    font-size: 12px;
  }

  .modal {
    padding: 1.2rem;
    width: 75%;
    font-size: 12px;
  }
}

.nav {
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  top: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  background-color: #f3f3f3;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.3);
  padding: 1rem;
  z-index: 10;
}

.nav__links {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  list-style: none;
}

.logo {
  font-size: 20px;
}

.menu {
  width: 30%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.nav__links a {
  text-decoration: none;
  color: black;
  padding: 5px;
}

.nav__links a:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  transition: all 0.2s;
}

.active {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  border-bottom: 2px solid black;
  transition: all 0.2s;
}

@media screen and (max-width: 768px) {
  .menu {
    width: 50%;
  }

  .logo {
    font-size: 18px;
  }
}

@media screen and (max-width: 520px) {
  .menu {
    width: 100%;
  }

  .logo {
    display: none;
  }
}

.loader {
  position: absolute;
  top: 50vh;
  left: 50%;
  -webkit-transform: translate(-50%, -30vh);
          transform: translate(-50%, -30vh);
  z-index: 2;
  font-size: 2rem;
  text-align: center;
}

.loader img {
  height: 100px;
  padding: 10px;
  object-fit: cover;
  -webkit-animation: load 3s linear infinite;
          animation: load 3s linear infinite;
}

@-webkit-keyframes load {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes load {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

